import { IReceptiionistSchedules, ActionTypes } from './types';

export function getReceptionistSchedulesRequest(
  schedules: IReceptiionistSchedules,
) {
  return {
    type: ActionTypes.receptionistSchedulesRequest,
    payload: { schedules },
  };
}

export function receptiionistSchedulesSuccess(response: any) {
  return {
    type: ActionTypes.receptiionistSchedulesSuccess,
    payload: { response },
  };
}

export function receptiionistSchedulesFailure(response: any) {
  return {
    type: ActionTypes.receptiionistSchedulesFailure,
    payload: { response },
  };
}
