import React, { Suspense, lazy } from 'react';
import { Switch } from 'react-router-dom';

import NotFoundPage from 'components/NotFoundPage';
import LoadingPage from 'components/LoadingPage';
import AppRoutes from './Route';
import { Constansts } from './constants';

const SignIn = lazy(() => import('modules/login/pages'));
const RecoverPassword = lazy(() => import('modules/RecoverPassword/view'));

const DashboardAdmin = lazy(() => import('modules/home/modules/admin/pages'));

const DashboardProfessional = lazy(
  () => import('modules/home/modules/professional/pages'),
);

const DashboardReceptionist = lazy(
  () => import('modules/home/modules/receptionist/pages'),
);

const Routes: React.FC = () => (
  <>
    <Suspense fallback={<div />}>
      <Switch>
        <AppRoutes path={Constansts.login} exact component={SignIn} />
        <AppRoutes
          path={Constansts.recuperarSenha}
          component={RecoverPassword}
        />
        <AppRoutes
          path={Constansts.adminDashboard}
          component={DashboardAdmin}
          isPrivate
        />
        <AppRoutes
          path={Constansts.professionalDashboard}
          component={DashboardProfessional}
          isPrivate
        />
        <AppRoutes
          path={Constansts.recepcionistaDashboard}
          component={DashboardReceptionist}
          isPrivate
        />
        <AppRoutes path="*" component={NotFoundPage} />
      </Switch>
    </Suspense>
    <LoadingPage />
  </>
);

export default Routes;
