import { all, takeLatest, select, call, put } from 'redux-saga/effects';
import { AxiosResponse } from 'axios';
import { Constansts } from 'routes/constants';
import { RequestSkwUtils } from 'core/requestSkwUtils';
import { MockResponse } from 'mock/model/mockresponse';
import {
  signInRequest,
  signInSuccess,
  signFailure,
  signOut as MySignOut,
} from '../states/auth/actions';
import { IState } from '../../../store/index';
import api from '../../../core/api';
import { ActionTypes } from '../states/auth/types';
import history from '../../../core/history';

type signInTYPE = ReturnType<typeof signInRequest>;
interface ISignInResponse {
  [x: string]: any;
  data: any;
}

function* signIn({ payload }: signInTYPE) {
  const doLoginRequest = 'RAUsersSP.doLogin';

  try {
    const { signin } = payload;

    const response: AxiosResponse<ISignInResponse> = yield call(
      api.post,
      Constansts.convertServiceNameToRequest(doLoginRequest),
      Constansts.convertServiceNameToBody(doLoginRequest, {
        login: signin.email,
        senha: signin.password,
      }),
    );

    // const temp = new MockResponse();
    // temp.data = {
    //   success: 'true',
    //   status: '1',
    //   callID: '5F4FFBD1DD000180D9EA59F37FF83C4C',
    //   requestBody: {
    //     token: '14682683-7b99-4b96-abd4-be62c6578211',
    //     idUsuario: 6,
    //     ativo: true,
    //     nome: 'Macceu',
    //     permissao: Constansts.ADMIN,
    //   },
    // };

    // const response = temp;

    api.defaults.headers.Authorization = response.data?.requestBody?.token;
    if (
      RequestSkwUtils.checkisSucess(
        response?.data?.requestBody || response?.data,
      )
    ) {
      yield put(signInSuccess(response?.data?.requestBody));
    } else {
      RequestSkwUtils.setErroMessage(response?.data?.statusMessage);
      yield put(signFailure(response?.data?.statusMessage));
    }
  } catch (err) {
    console.log(err);
    RequestSkwUtils.setErroMessage(
      err.response?.data?.statusMessage ?? err.response?.data,
    );
    yield put(signFailure(err.response?.data));
  }
}
