import { toast } from 'react-toastify';

interface Message {
  content: string;
  type?: 'default' | 'error' | 'info' | 'success' | 'warn';
}

export function showMessage(message: Message): void {
  switch (message.type) {
    case 'error':
      toast.error(message.content);
      break;

    case 'info':
      toast.info(message.content);
      break;

    case 'success':
      toast.success(message.content);
      break;

    case 'warn':
      toast.warn(message.content);
      break;

    default:
      toast(message.content);
  }
}
