import { IAdminSchedules, ActionTypes } from './types';

export function getAdminSchedulesRequest(schedules: IAdminSchedules) {
  return {
    type: ActionTypes.adminschedulesRequest,
    payload: { schedules },
  };
}

export function adminSchedulesSuccess(response: any) {
  return {
    type: ActionTypes.adminschedulesSuccess,
    payload: { response },
  };
}

export function adminSchedulesFailure(response: any) {
  return {
    type: ActionTypes.adminschedulesFailure,
    payload: { response },
  };
}
