import { Reducer } from 'redux';
import produce from 'immer';
import { IAuthState, ActionTypes } from './types';
/* eslint-disable consistent-return */
/* eslint-disable no-param-reassign */
const INITIAL_STATE: IAuthState = {
  token: '',
  signed: false,
  loading: false,
  resulted: { success: null, failure: null },
};

const auth: Reducer<IAuthState> = (state = INITIAL_STATE, action) => {
  return produce(state, draft => {
    switch (action.type) {
      case ActionTypes.signInRequest: {
        draft.loading = true;
        break;
      }
      case ActionTypes.signInSuccess: {
        draft.loading = false;
        draft.signed = true;
        draft.resulted = {
          success: action.payload?.response,
          failure: null,
        };
        break;
      }
      case ActionTypes.signFailure: {
        draft.loading = false;
        draft.signed = false;
        draft.resulted = { success: null, failure: action };
        break;
      }
      case ActionTypes.signOut: {
        draft.loading = false;
        draft.signed = false;
        draft.token = '';

        draft.resulted = { success: null, failure: null };
        break;
      }
      default: {
        return draft;
      }
    }
  });
};
export default auth;
