import { showMessage } from 'utils/message';

export class RequestSkwUtils {
  static checkSession = (body: any) => {
    return body.status === '3';
  };

  static checkisSucess = (body: any) => {
    console.log(`RESPOSTA DO SERVIDOR => `, body);
    return body?.status !== '0';
  };

  static setErroMessage = (body: any) => {
    showMessage({ type: 'error', content: body || 'sem resposta do servidor' });
  };
}
