import { all, takeLatest, call, put } from 'redux-saga/effects';
import { Constansts } from 'routes/constants';
import { RequestSkwUtils } from 'core/requestSkwUtils';
import api from 'core/api';
import {
  createUserRequest,
  createUserSuccess,
  createUserFailure,
  editUserSuccess,
  editUserFailure,
} from '../states/actions';
import { ActionTypes } from '../states/types';

type signInTYPE = ReturnType<typeof createUserRequest>;

function* createUser({ payload }: signInTYPE) {
  const doLoginRequest = 'RAUsersSP.createUser';

  try {
    const { data } = yield call(
      api.post,
      Constansts.convertServiceNameToRequest(doLoginRequest),
      Constansts.convertServiceNameToBody(doLoginRequest, payload.user),
    );

    if (RequestSkwUtils.checkisSucess(data?.requestBody || data)) {
      yield put(createUserSuccess(data?.requestBody));
    } else {
      RequestSkwUtils.setErroMessage(data?.statusMessage);
      yield put(createUserFailure(data?.statusMessage));
    }
  } catch (err) {
    RequestSkwUtils.setErroMessage(err.data?.statusMessage);
    yield put(createUserFailure(err.data));
  }
}

function* editUser({ payload }: signInTYPE) {
  const doLoginRequest = 'RAUsersSP.editUser';

  try {
    const { data } = yield call(
      api.post,
      Constansts.convertServiceNameToRequest(doLoginRequest),
      Constansts.convertServiceNameToBody(doLoginRequest, payload.user),
    );

    if (RequestSkwUtils.checkisSucess(data?.requestBody || data)) {
      yield put(editUserSuccess(data?.requestBody));
    } else {
      RequestSkwUtils.setErroMessage(data?.statusMessage);
      yield put(editUserFailure(data?.statusMessage));
    }
  } catch (err) {
    RequestSkwUtils.setErroMessage(err.data?.statusMessage);
    yield put(editUserFailure(err.data));
  }
}

export default all([
  takeLatest(ActionTypes.createUserRequest, createUser),
  takeLatest(ActionTypes.editUserRequest, editUser),
]);
