import { all, takeLatest, select, call, put } from 'redux-saga/effects';
import { RequestSkwUtils } from 'core/requestSkwUtils';
import { MockResponse } from 'mock/model/mockresponse';
import api from 'core/api';
import {
  getAdminSchedulesRequest,
  adminSchedulesSuccess,
  adminSchedulesFailure,
} from '../states/schedules/actions';
import { ActionTypes } from '../states/schedules/types';

type scheduleTYPE = ReturnType<typeof getAdminSchedulesRequest>;
interface ISchedulesResponse {
  [x: string]: any;
  data: any;
}
function* getSchedules({ payload }: scheduleTYPE) {
  const serviceSchedules = 'RAUsersSP.get';

  try {
    // const response: AxiosResponse<ISchedulesResponse> = yield call(
    //   api.post,
    //   Constansts.convertServiceNameToRequest(serviceSchedules),
    //   Constansts.convertServiceNameToBody(serviceSchedules, {
    //
    //   }),
    // );

    const temp = new MockResponse();
    temp.data = {
      success: 'true',
      status: '1',
      callID: '5F4FFBD1DD000180D9EA59F37FF83C4C',
      requestBody: MockResponse.data,
    };

    const response = temp;

    if (
      RequestSkwUtils.checkisSucess(
        response?.data?.requestBody || response?.data,
      )
    ) {
      yield put(adminSchedulesSuccess(response?.data?.requestBody));
    } else {
      RequestSkwUtils.setErroMessage(response?.data?.statusMessage);
      yield put(adminSchedulesFailure(response?.data?.statusMessage));
    }
  } catch (err) {
    console.log(err);
    RequestSkwUtils.setErroMessage(
      err.response?.data?.statusMessage ?? err.response?.data,
    );
    yield put(adminSchedulesFailure(err.response?.data));
  }
}
export default all([
  takeLatest(ActionTypes.adminschedulesRequest, getSchedules),
]);
