import { all } from 'redux-saga/effects';

import adminSchedule from 'modules/home/modules/admin/repository/schedulesSaga';
import professionalSchedule from 'modules/home/modules/professional/repository/schedulesSaga';
import receptionistSchedule from 'modules/home/modules/receptionist/repository/schedulesSaga';
import auth from '../../modules/login/repository/loginSaga';
import users from '../../modules/home/modules/admin/modules/users/repository/userSaga';

export default function* rootSaga() {
  return yield all([
    auth,
    users,
    adminSchedule,
    professionalSchedule,
    receptionistSchedule,
  ]);
}
