import { ActionTypes, IUser } from './types';

export function createUserRequest(user: IUser) {
  return {
    type: ActionTypes.createUserRequest,
    payload: { user },
  };
}

export function createUserSuccess(response: any) {
  return {
    type: ActionTypes.createUserSuccess,
    payload: { response },
  };
}

export function createUserFailure(response: any) {
  return {
    type: ActionTypes.createUserFailure,
    payload: { response },
  };
}

