import { Reducer } from 'redux';
import produce from 'immer';
import { ActionTypes, IProfessionalSchedulesState } from './types';
/* eslint-disable consistent-return */
/* eslint-disable no-param-reassign */
const INITIAL_STATE: IProfessionalSchedulesState = {
  loading: false,
  resulted: { success: null, failure: null },
};

const professionalSchedules: Reducer<IProfessionalSchedulesState> = (
  state = INITIAL_STATE,
  action,
) => {
  return produce(state, draft => {
    switch (action.type) {
      case ActionTypes.professionalSchedulesRequest: {
        draft.loading = true;
        break;
      }
      case ActionTypes.professionalSchedulesSuccess: {
        draft.loading = false;
        draft.resulted = {
          success: action.payload?.response,
          failure: null,
        };
        break;
      }
      case ActionTypes.professionalSchedulesFailure: {
        draft.loading = false;
        draft.resulted = { success: null, failure: action };
        break;
      }
      default: {
        return draft;
      }
    }
  });
};
export default professionalSchedules;
