import { combineReducers } from 'redux';

import auth from 'modules/login/states/auth/reducer';
import loading from 'components/LoadingPage/modules/states/reducer';
import users from 'modules/home/modules/admin/modules/users/states/reducer';
import adminSchedules from 'modules/home/modules/admin/states/schedules/reducer';
import professionalSchedules from 'modules/home/modules/professional/states/schedules/reducer';
import receptionistSchedules from 'modules/home/modules/receptionist/states/schedules/reducer';

export default combineReducers({
  auth,
  users,
  loading,
  adminSchedules,
  professionalSchedules,
  receptionistSchedules,
});
