import { IRegister, ISignIn, ActionTypes } from './types';

export function signInRequest(signin: ISignIn) {
  return {
    type: ActionTypes.signInRequest,
    payload: { signin },
  };
}

export function signInSuccess(response: any) {
  return {
    type: ActionTypes.signInSuccess,
    payload: { response },
  };
}

export function signFailure(response: any) {
  return {
    type: ActionTypes.signFailure,
    payload: { response },
  };
}

export function registerRequest(register: IRegister) {
  return {
    type: ActionTypes.registerRequest,
    payload: { register },
  };
}

export function signOut() {
  return {
    type: ActionTypes.signOut,
  };
}
