export class Constansts {
  static professional = '/profissional';

  static recepcionista = '/recepcionista';

  static admin = '/admin';

  static professionalDashboard = `${Constansts.professional}/dashboard`;

  static recepcionistaDashboard = `${Constansts.recepcionista}/dashboard`;

  static adminDashboard = `${Constansts.admin}/dashboard`;

  static login = '/';

  static recuperarSenha = '/recuperar_senha';

  static convertServiceNameToRequest = (_nameService: String) => {
    return `/rioarte-agendamento/portal?serviceName=${_nameService}&outputType=json&preventTransform=false`;
  };

  static convertServiceNameToBody = (_nameService: String, body: object) => {
    return {
      serviceName: _nameService,
      requestBody: body,
    };
  };

  static ADMIN = 'ADM';

  static PROFISSIONAL = 'PRF';

  static RECEPCIONISTA = 'ATD';

  // Administrador, Atendente, Profissional
  static getPermissionByUser = (_body: any, type: String): boolean => {
    console.log(`token => `, _body);
    return _body?.permissao === type;
  };
}
