import { Reducer } from 'redux';
import produce from 'immer';
import { ActionTypes as AuthRequest } from '../../../../modules/login/states/auth/types';
import { ILoading } from './types';
/* eslint-disable consistent-return */
/* eslint-disable no-param-reassign */
const INITIAL_STATE: ILoading = {
  isLoad: false,
};

const loading: Reducer<ILoading> = (state = INITIAL_STATE, action) => {
  return produce(state, draft => {
    switch (action.type) {
      case AuthRequest.signInRequest: {
        draft.isLoad = true;
        break;
      }
      case AuthRequest.signInSuccess: {
        draft.isLoad = false;
        break;
      }
      case AuthRequest.signFailure: {
        draft.isLoad = false;
        break;
      }
      default: {
        draft.isLoad = false;
        return draft;
      }
    }
  });
};
export default loading;
