import { IProfessionalSchedules, ActionTypes } from './types';

export function getProfessionalSchedulesRequest(
  schedules: IProfessionalSchedules,
) {
  return {
    type: ActionTypes.professionalSchedulesRequest,
    payload: { schedules },
  };
}

export function professionalSchedulesSuccess(response: any) {
  return {
    type: ActionTypes.professionalSchedulesSuccess,
    payload: { response },
  };
}

export function professionalSchedulesFailure(response: any) {
  return {
    type: ActionTypes.professionalSchedulesFailure,
    payload: { response },
  };
}
