import { Reducer } from 'redux';
import produce from 'immer';
import { ActionTypes, IReceptionistSchedulesState } from './types';
/* eslint-disable consistent-return */
/* eslint-disable no-param-reassign */
const INITIAL_STATE: IReceptionistSchedulesState = {
  loading: false,
  resulted: { success: null, failure: null },
};

const receptionistSchedules: Reducer<IReceptionistSchedulesState> = (
  state = INITIAL_STATE,
  action,
) => {
  return produce(state, draft => {
    switch (action.type) {
      case ActionTypes.receptionistSchedulesRequest: {
        draft.loading = true;
        break;
      }
      case ActionTypes.receptiionistSchedulesSuccess: {
        draft.loading = false;
        draft.resulted = {
          success: action.payload?.response,
          failure: null,
        };
        break;
      }
      case ActionTypes.receptiionistSchedulesFailure: {
        draft.loading = false;
        draft.resulted = { success: null, failure: action };
        break;
      }
      default: {
        return draft;
      }
    }
  });
};
export default receptionistSchedules;
