import { Reducer } from 'redux';
import produce from 'immer';
import { ActionTypes, IUsersState } from './types';

const INITIAL_STATE: IUsersState = {
  loading: false,
  resulted: { success: null, failure: null },
};

/* eslint-disable consistent-return */
/* eslint-disable no-param-reassign */
const users: Reducer<IUsersState> = (state = INITIAL_STATE, action) => {
  return produce(state, draft => {
    switch (action.type) {
      case ActionTypes.createUserRequest: {
        draft.loading = true;
        break;
      }
      case ActionTypes.createUserSuccess: {
        draft.loading = false;
        draft.resulted = {
          success: action.payload?.response,
          failure: null,
        };
        break;
      }
      case ActionTypes.createUserFailure: {
        draft.loading = false;
        draft.resulted = { success: null, failure: action };
        break;
      }
      case ActionTypes.editUserRequest: {
        draft.loading = true;
        break;
      }
      case ActionTypes.editUserSuccess: {
        draft.loading = false;
        draft.resulted = {
          success: action.payload?.response,
          failure: null,
        };
        break;
      }
      case ActionTypes.editUserFailure: {
        draft.loading = false;
        draft.resulted = { success: null, failure: action };
        break;
      }
      case ActionTypes.listUsersRequest: {
        draft.loading = true;
        break;
      }
      case ActionTypes.listUsersSuccess: {
        draft.loading = false;
        draft.resulted = {
          success: action.payload?.response,
          failure: null,
        };
        break;
      }
      case ActionTypes.listUsersFailure: {
        draft.loading = false;
        draft.resulted = { success: null, failure: action };
        break;
      }
      case ActionTypes.deleteUserRequest: {
        draft.loading = true;
        break;
      }
      case ActionTypes.deleteUserSuccess: {
        draft.loading = false;
        draft.resulted = {
          success: action.payload?.response,
          failure: null,
        };
        break;
      }
      case ActionTypes.deleteUserFailure: {
        draft.loading = false;
        draft.resulted = { success: null, failure: action };
        break;
      }
      default: {
        return draft;
      }
    }
  });
};
export default users;
