import { createStore, applyMiddleware } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';
import createSagaMiddleware from 'redux-saga';
import { persistStore } from 'redux-persist';
import { IAdminSchedulesState } from 'modules/home/modules/admin/states/schedules/types';
import { IProfessionalSchedulesState } from 'modules/home/modules/professional/states/schedules/types';
import { IReceptionistSchedulesState } from 'modules/home/modules/receptionist/states/schedules/types';
import rootReducer from './core/rootReducer';
import rootSaga from './core/rootSaga';
import persistReducers from './persistReducers';

import { IAuthState } from '../modules/login/states/auth/types';
import { IUsersState } from '../modules/home/modules/admin/modules/users/states/types';

import { ILoading } from '../components/LoadingPage/modules/states/types';

export interface IState {
  auth: IAuthState;
  users: IUsersState;
  loading: ILoading;
  adminSchedules: IAdminSchedulesState;
  professionalSchedules: IProfessionalSchedulesState;
  receptionistSchedules: IReceptionistSchedulesState;
}

const sagaMiddleware = createSagaMiddleware();

const middlewares = [sagaMiddleware];

export const store = createStore(
  persistReducers(rootReducer),
  composeWithDevTools(applyMiddleware(...middlewares)),
);
export const persistor = persistStore(store);

sagaMiddleware.run(rootSaga);
