/* eslint-disable no-nested-ternary */
import React from 'react';
import { useSelector } from 'react-redux';
import {
  Route as ReactDOMRoute,
  RouteProps as ReactDOMRouteProps,
  Redirect,
} from 'react-router-dom';
import { IAuthState } from '../modules/login/states/auth/types';
import { IState } from '../store';
import { Constansts } from './constants';

interface RouteProps extends ReactDOMRouteProps {
  isPrivate?: boolean;
  component: any;
}

const Route: React.FC<RouteProps> = ({
  isPrivate,
  component: Component,
  ...rest
}) => {
  const { signed, resulted } = useSelector<IState, IAuthState>(state => {
    return state.auth;
  });

  // substituir isso aqui por uma roule de verdade
  const isAdmin = Constansts.getPermissionByUser(
    resulted?.success,
    Constansts.ADMIN,
  );
  const isRecepcionista = Constansts.getPermissionByUser(
    resulted?.success,
    Constansts.RECEPCIONISTA,
  );

  if (!signed && isPrivate) {
    return <Redirect to={Constansts.login} />;
  }
  if (signed && !isPrivate && !isAdmin) {
    if (isRecepcionista) {
      return <Redirect to={Constansts.recepcionistaDashboard} />;
    }
    return <Redirect to={Constansts.professionalDashboard} />;
  }
  if (signed && !isPrivate && isAdmin) {
    return <Redirect to={Constansts.adminDashboard} />;
  }

  return (
    <ReactDOMRoute
      {...rest}
      render={props => (
        <div>
          {(props.location.pathname.includes(Constansts.professional) ||
            props.location.pathname.includes(Constansts.recepcionista)) &&
          isAdmin ? (
            <Redirect to={Constansts.adminDashboard} />
          ) : props.location.pathname.includes(Constansts.admin) && !isAdmin ? (
            props.location.pathname.includes(Constansts.recepcionista) ? (
              <Redirect to={Constansts.recepcionistaDashboard} />
            ) : (
              <Redirect to={Constansts.professionalDashboard} />
            )
          ) : (
            <Component {...props} />
          )}
        </div>
      )}
    />
  );
};

export default Route;
