import React from 'react';
import { useSelector } from 'react-redux';
import { IState } from '../../store';
import { ILoading } from './modules/states/types';

import { Loader, LoaderBar, Container } from './styles';

const LoadingPage: React.FC = () => {
  const loadState = useSelector<IState, ILoading>(state => {
    return state.loading;
  });

  if (loadState.isLoad) {
    return (
      <Container>
        <Loader>
          <LoaderBar />
        </Loader>
      </Container>
    );
  }
  return null;
};

export default LoadingPage;
